import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { VarsPage } from '../pages/vars/vars';
import { ArithPage } from '../pages/arith/arith';
import { ArraysPage } from '../pages/arrays/arrays';
import { ConditionalsPage } from '../pages/conditionals/conditionals';
import { FunctionsPage } from '../pages/functions/functions';
import { LoopsPage } from '../pages/loops/loops';
import { ObjectsPage } from '../pages/objects/objects';
import { OperatorsPage } from '../pages/operators/operators';
import { TypesPage } from '../pages/types/types';


@NgModule({
  declarations: [
    MyApp,
    HomePage,
    VarsPage,
    ArithPage,
    ArraysPage,
    ConditionalsPage,
    FunctionsPage,
    LoopsPage,
    ObjectsPage,
    OperatorsPage,
    TypesPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    VarsPage,
    ArithPage,
    ArraysPage,
    ConditionalsPage,
    FunctionsPage,
    LoopsPage,
    ObjectsPage,
    OperatorsPage,
    TypesPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



