import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { VarsPage } from '../vars/vars';
import { ArithPage } from '../arith/arith';
import { ArraysPage } from '../arrays/arrays';
import { ConditionalsPage } from '../conditionals/conditionals';
import { FunctionsPage } from '../functions/functions';
import { LoopsPage } from '../loops/loops';
import { ObjectsPage } from '../objects/objects';
import { OperatorsPage } from '../operators/operators';
import { TypesPage } from '../types/types';
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  vars: any;
  arith: any;
  arrays: any;
  conditionals: any;
  functions: any;
  loops: any;
  objects: any;
  operators: any;
  types: any;

  constructor(public navCtrl: NavController) {

    this.arith = ArithPage;
    this.vars = VarsPage;
    this.arrays = ArraysPage;
    this.conditionals = ConditionalsPage;
    this.functions = FunctionsPage;
    this.loops = LoopsPage;
    this.objects = ObjectsPage;
    this.operators = OperatorsPage;
    this.types = TypesPage;
  }

}



